.footer {
  position: sticky;
  z-index: -1;
  bottom: 0;

  height: 200px;

  background-image: url("../images/footer-bg.webp");
  background-repeat: no-repeat;
  background-position: right 20% bottom;
  background-size: cover;

  @include on-tablet {
    height: 480px;
  }

  @include on-desktop {
    height: 555px;
  }
}
